.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: rgb(127, 226, 140);
  margin: 0;
  padding: 0 20px;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  display: flex;
  gap: 100px;
  
  
  
  
}.icon{
  font-size: 25px;
}

.lists{
  
  padding-left: 90px;
  box-sizing: border-box;
  display: flex;
  gap:30px;
  list-style: none;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  text-align: center;
  
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
